# Message

Message

> XCUI`Vue.prototype``$Message`, XCUI `this.$Message.info('message')`
> `import Message from 'xcui/components/message'` 
> MessageXCUI[Vue ](https://vuejs.org/v2/guide/plugins.html), ****`Vue.use(Message)` 


## 

::: demo 3s

```html

<tpl>
    <x-button type="primary" @click="info"></x-button>
</tpl>

<script>
    export default {
        methods: {
            info() {
                this.$Message.info('');
            }
        }
    };
</script>

```

:::

## 

::: demo `info()` `success()` `warning()``error`

```html

<tpl>
    <x-button type="success" @click="success"></x-button>
    <x-button type="warning" @click="warning"></x-button>
    <x-button type="danger" @click="error"></x-button>
</tpl>

<script>
