## 

::: demo 10`Message.config()`API

```html

<tpl>
    <x-button type="info" @click="delay">10s</x-button>
</tpl>

<script>
    export default {
        methods: {
            delay() {
                this.$Message.info('10', 10000);
            }
        }
    }
</script>

```

:::

## 

::: demo 

```html

<tpl>
    <x-button type="info" @click="callback"></x-button>
</tpl>

<script>
    export default {
        methods: {
            callback() {
                this.$Message.warning('', 3000, () => {
                    this.$Message.info('');
                });
            }
        }
    }
</script>

```

:::

## 

### `Message``Vue.use(xcui)`, 

```javascript
import Vue from 'vue';

import { Message } from 'xcui/components/message';

Vue.use(Message);
```

## API



```javascript
// 
this.$Message.info(content, duration, onClose);

// 
this.$Message.success(content, duration, onClose);

// 
this.$Message.warning(content, duration, onClose);

// 
this.$Message.error(content, duration, onClose);
```

|  |  |  |  |  |
|-----|-----|-----|-----|----|
|content|String||||
|duration|Number|3000|: , 30000||
|onClose|Function||||

## 

`Message.config(options)`1s

`Message.destroy()` `Message`

```javascript
// 50px, 1s

this.$Message.config({
    top: 50,
    duration: 1000
})

// `Message`

this.$Message.destroy();

```
