## Props

### Row Props

|  |  |  |  |  ||
|-----|-----|-----|-----|----|----|----|
|gutter|Number|0||||
|type|String||||`flex`|
|align|String||`flex`||`top`, `middle`, `bottom`|
|justify|String||`flex`||`start`, `end`, `center`, `space-around`, `space-between`|
|class|String||class|||

### Col Props
|  |  |  |  |  ||
|-----|-----|-----|-----|----|----|----|
|span|Number||0~24 0 `display:none`|||
|order|Number||`flex`|||
|offset|Number|||||
|push|Number|||||
|pull|Number|||||
|xs|Number, Object||`< 768px` |||
|sm|Number, Object||`>= 768px` |||
|md|Number, Object||`>= 992px` |||
|lg|Number, Object||`>= 1200px` |||


