## Select Props

|  |  |  |  |  ||
|-----|-----|-----|-----|-----|-----|-----|
|value|String, Array||`v-model`|||
|size|String||input||large, small|
|placeholder|String|||||
|disabled|Boolean|false|||true, false|
|multiple|Boolean|false|||true, false|
|multipleLimit|Number|0|0||
|allowClear|Boolean|false|||true, false|

## Select Events

|||||
|---|---|---|---|
|change|||`@change`|

