# Loading 

, 

> `Loading`XCUIXCUI`v-loading`
>  ****`Loading`XCUI[Vue ](https://vuejs.org/v2/guide/plugins.html),`Vue.use(Loading)` 

## 

::: demo `v-loading``Boolean`Loading `body` `DOM`  `body` 

```html

<tpl>
    <div v-loading.body="loading" style="width: 400px;height: 60px;padding: 30px; outline: 1px dotted #eee;">
        <img :src="imgPara">
    </div>
</tpl>

<script>
    export default {
        data() {
            return {
                loading: true
            }
        }
    }
</script>

```

:::

## 

::: demo Loading`x-loading-type``grid``dot`

```html

<tpl>
    <div v-loading="loading" x-loading-type="grid" style="width: 30%;display:inline-block;margin-top: 15px;padding: 30px; outline: 1px dotted #eee;">
        <img :src="imgPara">
    </div>

    <div v-loading="loading" x-loading-type="dot" style="width: 30%;display:inline-block;margin-top: 15px;padding: 30px; outline: 1px dotted #eee;">
        <img :src="imgPara">
    </div>
</tpl>

```

:::

## 

::: demo `fullscreen` body `lock`

```html

<tpl>
    <x-button type="primary" @click="openFullScreen" v-loading.fullscreen.lock="fullscreenLoading">
        3 
    </x-button>
</tpl>

<script>
    export default {
        data() {
            return {
                fullscreenLoading: false
            };
        },
        methods: {
            openFullScreen() {
                this.fullscreenLoading = true;
                setTimeout(() => {
                    this.fullscreenLoading = false;
                }, 3000);
            }
        }
    };
</script>

```

:::

## 

::: demo Loading`x-loading-text`

```html

<tpl>
    <div v-loading="loading" x-loading-text="" style="width: 30%;height: 60px;padding: 30px; outline: 1px dotted #eee;">
        <img :src="imgPara">
    </div>
</tpl>

```

:::

## 

::: demo Loading`x-loading-size`40

```html

<tpl>
    <div v-loading="loading" x-loading-size="20" style="width: 30%;height: 60px;padding: 30px; outline: 1px dotted #eee;">
        <img :src="imgPara">
    </div>
</tpl>

```

:::

## Directive Props

|  |  |  |  |  ||
|-----|-----|-----|-----|-----|-----|-----|
|v-loading|Boolean|-|||true, false|
|body|Boolean|false|body||true,false|
|fullscreen|Boolean|false|Loading||true,false|
|lock|Boolean|false|||true,false|
|x-loading-type|String||||circle(), dot, grid|
|x-loading-text|String|||||
|x-loading-size|String|40||||
|x-loading-class|String|||||


</template>

<script>
import imgPara from '../assets/short-paragraph.png';

export default {
    data() {
        return {
            loading: true,
            imgPara: imgPara,
            fullscreenLoading: false
        };
    },
    methods: {
        openFullScreen() {
            this.fullscreenLoading = true;
            setTimeout(() => {
                this.fullscreenLoading = false;
            }, 3000);
        }
    }
};
</script>


