<template lang="md">
# PageLoading 




> XCUI`Vue.prototype``$PageLoading`, XCUI `this.$PageLoading.start()`
> `import PageLoading from 'xcui/components/pageloading'` 
> PageloadingXCUI[Vue ](https://vuejs.org/v2/guide/plugins.html), ****`Vue.use(PageLoading)` 

## DEMO
        

::: demo `.start()``.done()``.error()` 

```html

<tpl>
        <x-button type="primary" @click="start">start</x-button>
        <x-button type="info" @click="done">done</x-button>
        <x-button type="danger" @click="error">error</x-button>
</tpl>

```

:::

## API

```javascript
// 
this.$PageLoading.start();

// 
this.$PageLoading.done();

// 
this.$PageLoading.error();

// 
this.$PageLoading.config({
   speed: 500,
   color: '#2c96ef' 
});
```

## Config Options
|  |  |  |  |  |
|-----|-----|-----|-----|----|
|speed|Number|350|||
|color|String|xcui|||


</template>


<script>
    export default {
        methods: {
            start() {
                this.$PageLoading.start();
            },
            done() {
                this.$PageLoading.done();
            },
            error() {
                this.$PageLoading.error();
            }
        }
    };
</script>


