## Popconfirm 

::: demo `modal``confirm``content``confirmLoading`loadingdemo

```html

<tpl>
    <x-popover confirm
        placement="top" 
        title=""
        :on-ok="ok"
        :on-cancel="cancel">
        <x-button :loading="loading"></x-button>
    </x-popover>
</tpl>

<script>
    export default {
        methods: {
            ok(e, popover) {
                e.preventDefault();
                popover.confirmLoading = true;
                setTimeout(() => {
                    this.$Message.success('');
                    popover.showPopper = false;
                    popover.confirmLoading = false;
                }, 2000);
            },
            cancel(e) {
                this.$Message.info('');
            }
        }
    };
</script>

```

:::


## 

::: demo `content`slotslot`content`

```html

<tpl>
    <x-popover placement="top" title="">
        <div slot="content"><br></div>
        <x-button></x-button>
    </x-popover>
</tpl>

```

:::

## 

::: demo `transition`

```html

<tpl>
    <x-popover placement="top" transition="my-fade" title="Title" :content="content">
        <x-button></x-button>
    </x-popover>
</tpl>

<script>
    export default {
        data() {
            return {
                content: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. '
            }
        }
    }
</script>

<style>
    .my-fade-enter-active, .my-fade-leave-active {
        transition: opacity .5s
    }
    .my-fade-enter, .my-fade-leave-to {
        opacity: 0
    }
</style>

```

:::


  

## Props

|  |  |  |  |  ||
|-----|-----|-----|-----|-----|-----|-----|
|title|String|||||
|content|String|||||
|trigger|String|click|||hover,focus,click|
|placement|String|bottom|||top/top-start/top-end/bottom/bottom-start/bottom-end/left/left-start/left-end/right/right-start/right-end|
|offset|Number|0||||
|transition|String|popover-zoom|css[Vue](https://vuejs.org/v2/guide/transitions.html#CSS-Transitions)|||
|visible-arrow|Boolean|true|Tooltip||true, false|
|open-delay|Number|200||||
|popper-class|String||tooltip|||
|confirm|Boolean|false||||
|on-ok|Function||`confirm`,(event, VueComponent)|||
|on-cancel|Function||`confirm`,event|||
|ok-text|String||`confirm`|||
|cancel-text|String||`confirm`|||

