<template lang="md">
# DateTimePicker 




## 

::: demo `x-date-picker``type``datetime`, 

```html

<tpl>
    <div style="width:240px">
        <x-date-picker type="datetime" v-model="model1" placeholder=""></x-date-picker>
    </div>
</tpl>

<script>
    export default {
        data() {
            return {
                model1: ''
            }
        }
    }
</script>

```

:::

## 

::: demo `pickerOptions``shortcuts`

```html

<tpl>
    <div style="width:240px">
        <x-date-picker type="datetime" v-model="model2" align="right" placeholder="" :picker-options="pickerOption1">
        </x-date-picker>
    </div>
</tpl>

<script>
    export default {
        data() {
            return {
                model2: '',
                pickerOption1: {
                    shortcuts: [{
                        text: '',
                        onClick(picker) {
                            picker.$emit('pick', new Date());
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() - 3600 * 1000 * 24);
                            picker.$emit('pick', date);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', date);
                        }
                    }]
                }
            }
        }
    }
</script>

```

:::

## 

::: demo `type=datetimerange`

```html

<tpl>
    <div style="width: 350px">
        <x-date-picker type="datetimerange" range-separator="  " v-model="model3" placeholder="">
        </x-date-picker>
    </div>
</tpl>

<script>
    export default {
        data() {
            return {
                model3: [new Date(2016, 11, 12, 10, 10), new Date(2016, 11, 13, 0, 45)]
            }
        }
    }
</script>

```

:::


## 

::: demo `pickerOptions``shortcuts`

```html

<tpl>
    <div style="width: 350px">
        <x-date-picker type="datetimerange" v-model="model4" placeholder="" :picker-options="pickerOptions2">
        </x-date-picker>
    </div>
</tpl>

<script>
    export default {
        data() {
            return {
                model4: '',
                pickerOptions2: {
                    shortcuts: [{
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                            picker.$emit('pick', [start, end]);
                        }
                    }]
                }
            }
        }
    }
</script>

```

:::


## DateTimePicker Props

|  |  |  |   |  |  |
|-----|-----|-----|-----|----|----|
|v-model|String, Array[Date, Date]()|||||
|readonly|Boolean|false||||
|disabled|Boolean|false|||true, false|
|editable|Boolean|false|||true, false|
|clearable|Boolean|true|||true, false|
|size|String||||large, small|
|placeholder|String|||||
|type|String|date|||year/month/date/week/datetime/daterange/datetimerange|
|format|String|yyyy-MM-dd|||yyyy, MM, dd, HH, mm, ss|
|align|String|left|||left, center, right|
|popper-class|String|||||
|picker-options|Object|||||
|range-separator|String|-||||

## Picker Options

|  |  |  |  |  |
|---|---|---|---|---|
|shortcuts|object[]|demo|||
|disabledDate|Function|Boolean|||
|firstDayOfWeek|Number||7|17|

## Shortcuts

|  |  |  |  |  |
|---|---|---|---|---|
|text|String||||
|onClick|Function|vm, `pick``vm.$emit('pick', new Date())`|||

## DateTimePicker Events
|||||
|---|---|---|---|
|change|||`@change`|

</template>

