<template lang="md">
# Suggestion 




## 

::: demo `v-model``suggestion``````allow-clear`

```html

<tpl>
    <x-suggestion v-model="state1" placeholder="" :suggestions="sug1"></x-suggestion>
    <p style="margin-top:15px;">{{state1}}</p>
</tpl>

<script>
  export default {
      data() {
          return {
              state1: {},
              sug1: ['','','','','','','','','','','','']
          }
      }
  }
</script>
```

:::

## Object

::: demo `[{text:'text', value:'value'}, ...]` 

```html

<tpl>
    <x-suggestion v-model="state2" placeholder="" :suggestions="sug2"></x-suggestion>
    <p style="margin-top:15px;">{{state2}}</p>
</tpl>

<script>
    export default {
        data() {
            return {
                state2: {},
                sug2: [
                    {text: '', value: '131'},
                    {text: '', value: '289'},
                    {text: '', value: '125'},
                    {text: '', value: '121'},
                    {text: '', value: '179'},
                    {text: '', value: '75'},
                    {text: '', value: '146'}
                ]
            }
        }
    }
</script>

```

:::

## 

::: demo `input-callback`, `v-model` `text``text`

```html

<tpl>
    <x-suggestion v-model="state3" placeholder="" :suggestions="sug3" :input-callback="request"></x-suggestion>
    <p style="margin-top:15px;">{{state3}}</p>
</tpl>

<script>
    export default {
        data() {
            return {
                state3: {},
                sug3: []
            }
        },
        methods: {
            request() {
                const v = this.state3.text;
                this.sug3 = !v ? [] : [
                    {text: v, value: 1},
                    {text: v + v, value: 2},
                    {text: v + v + v, value: 3},
                    {text: v + v + v + v, value: 4}
                ];
            }
        }
    }
</script>

```

:::

## Props
|  |  |  |  |  ||
|-----|-----|-----|-----|-----|-----|-----|
|value|Object||`v-model`|||
|placeholder|String|||||
|disabled|Boolean|false||||
|suggestions|Array|[]||||
|inputCallback|Function||, |||
|allowClear|Boolean|true|||true, false|

## Events

|||||
|---|---|---|---|
|change|||`@change`|

</template>

<script>
  export default {
      data() {
          return {
              showHello: true,
              state1: {},
              sug1: [
                  '',
                  '',
                  '',
                  '',
                  '',
                  '',
                  '',
                  '',
                  '',
                  '',
                  '',
                  ''
              ],
              state2: {},
              sug2: [
                  {
                      text: '',
                      value: '131'
                  },
                  {
                      text: '',
                      value: '289'
                  },
                  {
                      text: '',
                      value: '125'
                  },
                  {
                      text: '',
                      value: '121'
                  },
                  {
                      text: '',
                      value: '179'
                  },
                  {
                      text: '',
                      value: '75'
                  },
                  {
                      text: '',
                      value: '146'
                  }
              ],
              state3: {},
              sug3: []
          };
      },
      watch: {
      },
      methods: {
          request() {
              const v = this.state3.text;
              this.sug3 = !v ? [] : [
                  {text: v, value: 1},
                  {text: v + v, value: 2},
                  {text: v + v + v, value: 3},
                  {text: v + v + v + v, value: 4}
              ];  
          }
      },
      mounted() {
          let me = this;
          window.bdsug = {
              sug(res) {
                  me.setSug(res.s);
              },
              sug2(res) {
                  me.setSug2(res.s);
              }
          };
      }
  };
</script>




