## 

::: demo `row-class-name``String`  `Function`

```html
<tpl>
    <div>
        <x-table :data="sourceData" :row-class-name="tableRowClassName">
            <x-table-column
                title=""
                prop="name"
            >
            </x-table-column>
            <x-table-column
                title=""
                prop="job"
            >
            </x-table-column>
            <x-table-column
                title=""
                prop="address"
            >
            </x-table-column>
        </x-table>
    </div>
</tpl>
<script>
    export default {
        data() {
            return {
                sourceData: [
                    {
                        name: '',
                        address: '17',
                        job: ''
                    }, {
                        name: '',
                        address: '17',
                        job: ''
                    }, {
                        name: '',
                        address: '17',
                        job: 'UI'
                    }
                ],
            };
        },
        methods: {
            tableRowClassName(row, index) {
                if (index === 0) {
                    return 'feStyle';
                }
                return '';
            }
        }
    };
</script>
<style type="text/css">
    .x-table .feStyle {
        background-color: #6BCFCD;
        color: #fff;
    }
</style>

```
:::

