## 

::: demo `x-table-column``type``radio`

```html
<tpl>
    <div>
        <x-table
            :data="sourceData"
            :initial-selected-value="selectedValue"
            @radio-change="onUpdateSelectedValue"
        >
            <x-table-column
                type="radio"
                prop="name"
                width="50px"
            >
            </x-table-column>
            <x-table-column
                title=""
                prop="name"
            >
            </x-table-column>
            <x-table-column
                title=""
                prop="job"
            >
            </x-table-column>
            <x-table-column
                title=""
                prop="address"
            >
            </x-table-column>
        </x-table>
        <p> {{selectedValue}}</p>
    </div>
</tpl>
<script>
    export default {
        data() {
            return {
                sourceData: [
                    {
                        name: '',
                        address: '17',
                        job: ''
                    }, {
                        name: '',
                        address: '17',
                        job: ''
                    }, {
                        name: '',
                        address: '17',
                        job: 'UI'
                    }
                ],
                selectedValue: ''
            };
        },
        methods: {
            onUpdateSelectedValue(value, dataList) {
                this.selectedValue = value;
                console.log(dataList);
            }
        }
    };
</script>
```
:::
