## Table Props
|  |  |  |  |  |
|-----|-----|-----|-----|----|
|data|Array|[]|||
|bordered|Boolean|false|||
|striped|Boolean|false|||
|empty-tip|String||||
|row-class-name|String or Function|-| className||
|initial-selected-value-list|Array|[]|typeselectionprop||
|initial-selected-value|String or Number|[]|typeradioprop||

## Table-column Props
|  |  |  |  |  |
|-----|-----|-----|-----|----|
|title|String|-|||
|type|String|-|column selectionradio||
|prop|String|-|||
|width|String|-|||
|class-name|String|-| className||
|single-line|Boolean|-| tooltips||

## Table Events
|  |  |  |
|-----|-----|----|
|radio-change|||
|selection-change|||


