<template lang="md">
# Pagination 



## 

::: demo `change-current``change-current`, `total`, `current-page-num`

```html

<tpl>
    <x-pagination
        @change-current="goToPage"
        :current-page-num="num1"
        :total="total"
        :page-size="pageSize">
    </x-pagination>
</tpl>

<script>
    export default {
        data() {
            return {
                num1: 1,
                pageSize: 10,
                total: 100
            };
        },
        methods: {
            goToPage(pageNo, oldPageNo) {
                this.num1 = pageNo;
            }
        }
    };
</script>

```
:::

## 



::: demo `show-sizer`, `show-total``sizer``change-pagesize``page-size`

```html

<tpl>
    <x-pagination
        show-sizer
        show-total
        @change-current="goToPage2"
        @change-pagesize="onChangeSize"
        :current-page-num="num2"
        :total="total"
        :page-size="pageSize">
    </x-pagination>
</tpl>

<script>
    export default {
        data() {
            return {
                num2: 1,
                pageSize: 10,
                total: 100
            };
        },
        methods: {
            goToPage2(pageNo, oldPageNo) {
                this.num2 = pageNo;
            },
            onChangeSize(pageSize) {
                this.pageSize = pageSize;
                this.goToPage2(1);// 
            }
        }
    };
</script>

```

:::


## 

::: demo `type="mini"`

```html

<tpl>
    <x-pagination
        show-total
        type="mini"
        @change-current="goToPage"
        :current-page-num="num1"
        :total="total"
        :page-size="pageSize">
    </x-pagination>
</tpl>

```

:::

        

## Props

|  |  |  |  |  ||
|-----|-----|-----|-----|-----|-----|-----|
|type| String| standard | |  | standard,mini |
|current-page-num| Number| 1 |  |  | |
|total|Number|0||||
|page-size|Number|20||||
|show-sizer|Boolean|false|||true, false|
|show-total|Boolean|false|||true, false|
|page-size-range| Array | [10, 20, 50, 100] | `sizer` `show-sizer``true` |||
|range-length| Number | 5 |   |  | |


##  Events
|||||
|---|---|---|---|
|change-current||(pageNum, oldPageNum) pageNum:; oldPageNum: ; |`@change-current`|
|change-pagesize||(pageSize) pageSize:; |`@change-pagesize`|

</template>

<script>
export default {
    name: 'xcui-pagination-demo',
    data() {
        return {
            num1: 1,
            num2: 1,
            pageSize: 10,
            list: [],
            total: 100
        };
    },
    methods: {
        goToPage(pageNo, oldPageNo) {
            this.num1 = pageNo;
        },
        goToPage2(pageNo, oldPageNo) {
            this.num2 = pageNo;
        },
        onChangeSize(pageSize) {
            this.pageSize = pageSize;// 
            this.goToPage2(1);// 
        }
    },
    mounted() {
        // this.goToPage(1);
    }
};
</script>

<style lang="less">
    .table{
        border: 1px solid #ddd;
        background-color: #fff;
        margin-top: 15px;
    }
</style>



