## 

::: demo 

```html

<tpl>
    <x-form ref="form2" :model="form2" :rules="rules2" label-width="80px" style="width:440px">
        <x-form-item label="" prop="name">
            <x-input icon="person" placeholder="" v-model="form2.name"></x-input>
        </x-form-item>
        <x-form-item label="" prop="password">
            <x-input icon="key" placeholder="" v-model="form2.password"></x-input>
        </x-form-item>
        <x-form-item prop="remember">
            <x-checkbox v-model="form2.remember"></x-checkbox>
            <x-button type="link" style="margin-left:150px"></x-button>
        </x-form-item>
        <x-form-item>
            <x-button type="primary" @click="submitForm('form2')"></x-button>
            <x-button @click="resetForm('form2')"></x-button>
        </x-form-item>
    </x-form>
</tpl>

<script>
    export default {
        data() {
            return {
                form2: {
                    name: '',
                    password: '',
                    remember: false
                },
                rules2: {
                    name: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    password: [
                        {required: true, message: '', trigger: 'blur'}
                    ]
                }
            }
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate(valid => {
                    if (valid) {
                        alert('submit!');
                    }
                    else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        }
    }
</script>

```

