> `rules`[async-validator](https://github.com/yiminghe/async-validator)


## 

::: demo `rules``validator`

```html

<tpl>
    <x-form ref="form6" :model="form6" :rules="rules6" label-width="80px" style="width:440px">
        <x-form-item label="" prop="age">
            <x-input v-model.number="form6.age" placeholder=""></x-input>
        </x-form-item>
    </x-form>
</tpl>

<script>
    export default {
        data() {
            const validatePass = (rule, value, callback) => {
                if (!value) {
                    return callback(new Error(''));
                }
                setTimeout(_ => {
                    if (!Number.isInteger(value)) {
                        callback(new Error(''));
                    }
                    else {
                        if (value < 18) {
                            callback(new Error('18'));
                        }
                        else {
                            callback();
                        }
                    }
                });
            };
            return {
                form6: {
                    age: ''
                },
                rules6: {
                    age: [
                        {validator: validatePass, trigger: 'blur'}
                    ]
                },
            }
        }
    }
</script>

```

:::

## 

::: demo 

```html

<tpl>
    <x-form ref="form7" :model="form7" label-width="80px" style="width:440px">
        <x-form-item 
            v-for="(name, index) in form7.names"
            :label="'' + (index + 1)"
            :key="name.key"
            :prop="'names.' + index + '.value'">
            <x-input v-model="name.value" style="width: 250px" placeholder=""></x-input>
            <x-button v-if="index > 0" size="small" @click="removeName(name)"></x-button>
        </x-form-item>
        <x-form-item>
            <x-button type="primary" @click="addName"></x-button>
        </x-form-item>
    </x-form>
</tpl>

<script>
    export default {
        data() {
            return {
                form7: {
                    names: [{
                        value: '', key: Date.now()
                    }]
                }
            }
        },
        methods: {
            addName() {
                this.form7.names.push({
                    value: '',
                    key: Date.now()
                });
            },
            removeName(item) {
                let index = this.form7.names.indexOf(item);
                if (index !== -1) {
                    this.form7.names.splice(index, 1);
                }
            }
        }
    }
</script>

```

