## Form Props

|  |  |  |  |  ||
|---|---|---|---|---|---|---|
|model|Object|||||
|rules|Object|||||
|inline|Boolean|false|||true,false|
|label-position|String|right|||right/left/top|
|label-width|String||||`80px`|
|show-message|Boolean|true|||true/false|

## Form Methods

|  |  |  |
|---|---|---|
|validate|      |  Function(callback: Function(boolean))|
|validateField |   | Function(prop: string, callback: Function(errorMessage: string))|
|resetFields|   | - |

## Form-Item Props

|  |  |  |  |  ||
|---|---|---|---|---|---|---|
|prop|String|model||Formmodel|
|label|String|||||
|label-width|String|||`50px`|
|required|Boolean|||true,false|
|rules|Object||||
|error|String|error,|||
|show-message|Boolean|true|||true/false|

## Form-Item Slot

|  |  | 
|---|---|
| - | Form-Item|
