<template lang="md">
# TimePicker 



##  

::: demo 

```html

<tpl>
    <div style="width: 180px">
         <x-time-select v-model="model1" 
            :picker-options="{
                start: '08:30',
                step: '00:15',
                end: '18:30'
                }" placeholder=""></x-time-select>
    </div>
   
</tpl>

<script>
    export default {
        data() {
            return {
                model1: ''
            }
        }
    }
</script>

```

:::

## 

::: demo `x-time-picker``selectableRange` 

```html

<tpl>
    <div style="width:180px">
        <x-time-picker v-model="model2"
            :picker-options="{
                selectableRange: '18:30:00 - 23:30:00'
            }" placeholder=""></x-time-picker>
    </div>
    
</tpl>

<script>
    export default {
        data() {
            return {
                model2: new Date(2016, 9, 10, 18, 40)
            }
        }
    }
</script>

```

:::

## 

::: demo 

```html

<tpl>
    <div style="width:180px;display:inline-block;">
        <x-time-select placeholder="" v-model="startTime" :picker-options="{
            start: '08:30',
            step: '00:15',
            end: '18:30'
            }"></x-time-select>
    </div>
    <div style="width:180px; display:inline-block;margin-left: 15px;">
        <x-time-select placeholder="" v-model="endTime" :picker-options="{
            start: '08:30',
            step: '00:15',
            end: '18:30',
            minTime: startTime
            }"></x-time-select>
    </div>
</tpl>

<script>
    export default {
        data() {
            return {
                startTime: '',
                endTime: ''
            }
        }
    }
</script>

```

:::

##  

::: demo `is-range``true`

```html

<tpl>
    <div style="width: 240px">
        <x-time-picker is-range v-model="model3" placeholder=""></x-time-picker>
    </div>
</tpl>

<script>
    export default {
        data() {
            return {
                model3: [new Date(2016, 9, 10, 8, 40), new Date(2016, 9, 10, 9, 40)]
            }
        }
    }
</script>

```

:::


## Timepicker Props

|  |  |  |   |  |  |
|-----|-----|-----|-----|----|----|
|v-model|String|||||
|readonly|Boolean|false||||
|disabled|Boolean|false|||true, false|
|editable|Boolean|false|||true, false|
|clearable|Boolean|true|||true, false|
|size|String||||large, small|
|placeholder|String|||||
|align|String|left|||left, center, right|
|popper-class|String|||||
|picker-options|Object|||||
|range-separator|String|-||||

## TimePicker Options

|  |  |  |  |  |
|---|---|---|---|---|
|selectableRange|String/Array|`'18:30:00 - 20:30:00'``['09:30:00 - 12:00:00', '14:30:00 - 18:30:00']`|||
|format|String|(TimePicker)||HHmmss|


## Timepicker Events
|||||
|---|---|---|---|
|change|||`@change`|

</template>

<script>
    export default {
        data() {
            return {
                model1: '',
                model2: new Date(2016, 9, 10, 18, 40),
                startTime: '',
                endTime: '',
                model3: [new Date(2016, 9, 10, 8, 40), new Date(2016, 9, 10, 9, 40)]
            };
        }
    };
</script>


