# Radio 



## 

- 
-  Select Radio 


## 

::: demo `v-model``label``v-model``radio``label`

```html

<tpl>
    <x-radio v-model="checked" label="1">1</x-radio>
    <x-radio v-model="checked" label="2">2</x-radio>
    <p style="margin-top:15px;">{{checked}}</p>
</tpl>

<script>
    export default {
        data() {
            return {
                checked: '1'
            }
        }
    }
</script>

```

:::

