## Radio Props

|  |  |  |  |  ||
|---|---|---|---|---|---|---|
|label|String, Number, Boolean||Radio `value`|||
|disabled|Boolean|false|||`true`, `false`|

## Radio-group Props

|  |  |  |  |  ||
|---|---|---|---|---|---|---|
|size|String||Radio||large,small|

## Radio-group Events

|||||
|---|---|---|---|
|change|`v-model`|Radio label|`@change`|

    
