<template lang="md">
# Switch 

/

## 

::: demo `v-model``Boolean`, 

```html

<tpl>
    <x-switch v-model="status1"></x-switch>
</tpl>

<script>
    export default {
        data() {
            return {
                status1: false
            };
        }
    };
</script>

```

:::

## 

::: demo `disabled``true`

```html

<tpl>
    <x-switch v-model="status2" :disabled="true"></x-switch>
</tpl>

<script>
    export default {
        data() {
            return {
                status2: true
            };
        }
    };
</script>

```

:::

## 

::: demo `on-text``off-text`

```html

<tpl>
    <x-switch v-model="status3" on-text="" off-text=""></x-switch>
    <br>
    <x-switch v-model="status4" on-text="on" off-text="off"></x-switch>
</tpl>

<script>
    export default {
        data() {
            return {
                status3: false,
                status4: false
            };
        }
    };
</script>

```

:::


## Props

|  |  |  |  |  ||
|-----|-----|-----|-----|----|----|----|
|v-model|Boolean||||true,false|
|disabled|Boolean|false|||true,false|
|on-text|String|||||
|off-text|String|||||

## Events

|||||
|---|---|---|---|
|change||(true or false|`@change`|


</template>
<script>
    export default {
        data() {
            return {
                status1: false,
                status2: true,
                status3: false,
                status4: false
            };
        }
    };
</script>

<style lang="less">
    .demo-switch {
        .x-switch {
            margin-bottom: 8px;
        }
    }
</style>


