# Notice 

`Message`, `Notice`

> XCUI`Vue.prototype``$Notice`, XCUI `this.$Notice.info('some message')`
> `import Notice from 'xcui/components/notice'` 
> NoticeXCUI[Vue ](https://vuejs.org/v2/guide/plugins.html), ****`Vue.use(Notice)` 

::: demo 4.5s`desc`

```html

<tpl>
    <x-button @click="notice0"></x-button>
    <x-button type="primary" @click="notice1"></x-button>
</tpl>

<script>
    export default {
        methods: {
            notice0() {
                this.$Notice.open({
                    title: '',
                    desc: ''
                });
            },
            notice1() {
                this.$Notice.open({
                    title: '',
                    desc: ''
                });
            }
        }
    };
</script>

```

:::


## 

::: demo  `.info()`, `.success()`, `.warning()`, `.error()` 

```html

