    <x-button type="info" @click="notice2">info</x-button>
    <x-button type="success" @click="notice3">success</x-button>
    <x-button type="warning" @click="notice4">warning</x-button>
    <x-button type="danger" @click="notice5">error</x-button>
</tpl>

<script>
    export default {
        methods: {
            notice2() {
                this.$Notice.info({
                    title: '',
                    desc: ''
                });
            },
            notice3() {
                this.$Notice.success({
                    title: '',
                    desc: ''
                });
            },
            notice4() {
                this.$Notice.warning({
                    title: '',
                    desc: ''
