            }
        }
    };
</script>

```

:::

## 

::: demo `duration`0`duration`

```html

<tpl>
    <x-button type="info" @click="notice6"></x-button>
</tpl>

<script>
    export default {
        methods: {
            notice6() {
                this.$Notice.info({
                    title: '',
                    desc: '',
                    duration: 0
                });
            }

        }
    };
</script>

```

:::

## API



```javascript
// 
this.$Notice.info(config);

// 
this.$Notice.success(config);

// 
this.$Notice.warning(config);

// 
this.$Notice.error(config);
```

### Config options

|  |  |  |  |  |
|-----|-----|-----|-----|----|
|title|String||||
|content|String||||
|duration|Number|4500|: , 45000||
|onClose|Function||||
|uuid|String||||




```javascript
// 
this.$Notice.config(options)

// 
this.$Notice.close(uuid)

// 
this.$Notice.destroy()

this.$Notice.config({
    top: 50,
    duration: 3
});
```


