# Modal 

 Modal 


## 

::: demo `x-modal``v-model`

```html

<tpl>
    <x-button type="primary" @click="modal1 = true"></x-button>
    <x-modal v-model="modal1" title="">
      <p></p> 
    </x-modal>
</tpl>

<script>
    export default {
        data() {
            return {
                modal1: false
            }
        }
    }
</script>

```

:::

## 

::: demo `header`, `footer``show-header``show-footer`

```html

<tpl>
    <x-button @click="modal2 = true"></x-button>
    <x-button @click="modal3 = true"></x-button>
    <x-button @click="modal4 = true"></x-button>
    <x-modal v-model="modal2">
        <div slot="header" style="text-align:center; font-size: 16px;color: #46c3c1">
            <x-icon name="help-circled"></x-icon>
            <span></span>
        </div>
        <p></p>   
        <div slot="footer" style="text-align:right">
            <x-button type="primary" @click="exec" size="large" :loading="isLoading">
            {{isLoading ? '...' : ''}}</x-button>
        </div>
    </x-modal>

    <x-modal v-model="modal3" :show-header="false">
        <div></div>
    </x-modal>

    <x-modal v-model="modal4" title="" :show-footer="false">
        <div></div>
    </x-modal>
</tpl>

<script>
    export default {
        data() {
            return {
                modal2: false,
                modal3: false,
                modal4: false
            }
        },
        methods: {
            exec() {
                this.isLoading = true;
                setTimeout(() => {
                    this.isLoading = false;
                    this.$Message.success('');
                    this.modal2 = false;
                }, 2000);
            }
        }
    }
</script>

```
:::




