        :styles="{
            top: '50%',
            transform: 'translateY(-50%)'}">
        <div></div>
    </x-modal>
    <x-modal v-model="modal6" title="20px"
        :styles="{top: '20px'}">
        <div></div>
    </x-modal>
</tpl>

<script>
    export default {
        data() {
            return {
                isLoading: false,
                modal5: false,
                modal6: false
            }
        }
    }
</script>

```

:::




