<template lang="md">
# DatePicker 




## 

>  `v-model``ISO string``2016-12-13T00:45:00.000Z`

::: demo , `pickerOptions``disabledDate`

```html

<tpl>
    <div style="width:180px">
        <x-date-picker v-model="model1" placeholder="" :picker-options="pickerOption1"></x-date-picker>
    </div>
</tpl>

<script>
    export default {
        data() {
            return {
                model1: '',
                pickerOption1: {
                    disabledDate(time) {
                        return time.getTime() < Date.now() - 24 * 60 * 60 * 1000;
                    }
                }
            }
        }
    }
</script>

```

:::

## 

::: demo `pickerOptions``shortcuts`

```html

<tpl>
    <div style="width:180px">
        <x-date-picker v-model="model2" align="left" placeholder="" :picker-options="pickerOption2">
        </x-date-picker>
    </div>
</tpl>

<script>
    export default {
        data() {
            return {
                model2: '',
                pickerOption2: {
                    shortcuts: [{
                        text: '',
                        onClick(picker) {
                            picker.$emit('pick', new Date());
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() - 60 * 60 * 1000 * 24);
                            picker.$emit('pick', date);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() - 60 * 60 * 1000 * 24 * 7);
                            picker.$emit('pick', date);
                        }
                    }]
                }
            }
        }
    }
</script>
```

:::

##  /  / 

::: demo `type``week/month/year`//

```html

<tpl>
    <x-row>
        <x-col span="6">
            <p></p>
            <x-date-picker v-model="model3" placeholder="" type="week" format="yyyy  WW ">
            </x-date-picker>
        </x-col>
        <x-col span="6" offset="3">
            <p></p>   
            <x-date-picker v-model="model4" placeholder="" type="month">
            </x-date-picker>
        </x-col>
        <x-col span="6" offset="3">
            <p></p>
            <x-date-picker v-model="model5" placeholder="" type="year">
            </x-date-picker>
        </x-col>
    </x-row>
</tpl>

<script>
    export default {
        data() {
            return {
                model3: '',
                model4: '',
                model5: ''
            }
        }
    }
</script>

```

:::

## 

::: demo `type="daterange"``ISO string`[Date1, Date2]

```html

<tpl>
    <div style="width: 240px">
        <x-date-picker type="daterange" v-model="model6" placeholder="">
        </x-date-picker>
    </div>
</tpl>

```

:::

##  

::: demo `pickerOptions``shortcuts`

```html

<tpl>
    <div style="width: 240px">
        <x-date-picker type="daterange" v-model="model7" placeholder="" :picker-options="pickerOptions3">
        </x-date-picker>
    </div>
</tpl>

<script>
    export default {
        data() {
            return {
                model7: '',
                pickerOptions3: {
                    shortcuts: [{
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                            picker.$emit('pick', [start, end]);
                        }
                    }]
                }
            }
        }
    }
</script>

```

:::



## DatePicker Props

|  |  |  |   |  |  |
|-----|-----|-----|-----|----|----|
|v-model|String|||||
|readonly|Boolean|false||||
|disabled|Boolean|false|||true, false|
|editable|Boolean|false|||true, false|
|clearable|Boolean|true|||true, false|
|size|String||||large, small|
|placeholder|String|||||
|type|String|date|||year/month/date/week/datetime/daterange/datetimerange|
|format|String|yyyy-MM-dd|||yyyy, MM, dd, HH, mm, ss|
|align|String|left|||left, right|
|popper-class|String|||||
|picker-options|Object|||||
|range-separator|String|-||||

## Picker Options

|  |  |  |  |  |
|---|---|---|---|---|
|shortcuts|object[]|demo|||
|disabledDate|Function|Boolean|||
|firstDayOfWeek|Number||7|17|

## Shortcuts

|  |  |  |  |  |
|---|---|---|---|---|
|text|String||||
|onClick|Function|vm, `pick``vm.$emit('pick', new Date())`|||

## DatePicker Events
|||||
|---|---|---|---|
|change|||`@change`|



</template>
<script>
export default {
    data() {
        return {
            model1: '',
            pickerOption1: {
                disabledDate(time) {
                    return time.getTime() < Date.now() - 24 * 60 * 60 * 1000;
                }
            },
            model2: '2016-12-13T00:45:00.000Z',
            pickerOption2: {
                shortcuts: [{
                    text: '',
                    onClick(picker) {
                        picker.$emit('pick', new Date());
                    }
                }, {
                    text: '',
                    onClick(picker) {
                        const date = new Date();
                        date.setTime(date.getTime() - 60 * 60 * 1000 * 24);
                        picker.$emit('pick', date);
                    }
                }, {
                    text: '',
                    onClick(picker) {
                        const date = new Date();
                        date.setTime(date.getTime() - 60 * 60 * 1000 * 24 * 7);
                        picker.$emit('pick', date);
                    }
                }]
            },
            model3: '',
            model4: '',
            model5: '',
            model6: '',
            model7: '',
            pickerOptions3: {
                shortcuts: [{
                    text: '',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                        picker.$emit('pick', [start, end]);
                    }
                }, {
                    text: '',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                        picker.$emit('pick', [start, end]);
                    }
                }, {
                    text: '',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                        picker.$emit('pick', [start, end]);
                    }
                }]
            }
        };
    }
};

</script>


