<template  lang="md">
# Input-number 



## 

::: demo `x-input-number``v-model``@change```

```html

<tpl>
    <x-input-number v-model="num1" :min="1" :max="10" @change="handleChange"></x-input-number>
</tpl>
<script>
    export default {
        data() {
            return {
                num1: 1
            };
        },
        methods: {
            handleChange(val, oldVal) {
                console.log(`val: ${val}`);
                console.log(`oldVal: ${oldVal}`);
            }
        }
    };
</script>

```

:::

## 

::: demo `disabled`

```html

<tpl>
    <x-input-number v-model="num2" :min="1" :max="10" :disabled="true"></x-input-number>
</tpl>
<script>
    export default {
        data() {
            return {
                num2: 1
            }
        }
    }
</script>

```

:::

## 

::: demo `step`/`step`

```html

<tpl>
    <x-input-number v-model="num3" :step="3"></x-input-number>
</tpl>

<script>
    export default {
        data() {
            return {
                num3: 1
            }
        }
    }
</script>
```

:::

## 

::: demo `large`, `small` `size`

```html

<tpl>
    <x-input-number v-model="num4" size="large"></x-input-number>
    <x-input-number v-model="num5"></x-input-number>
    <x-input-number v-model="num6" size="small"></x-input-number>
</tpl>

<script>
    export default {
        data() {
            return {
                num4: 1,
                num5: 1,
                num6: 1
            }
        }
    }
</script>

```

:::



## Props
|  |  |  |  |  ||
|-----|-----|-----|-----|----|----|----|
|value|Number||`v-model`|`v-model`||
|min|Number|0||||
|max|Number|Infinity||||
|step| Number|1||||
|disabled|Boolean|false|||true,false|
|size|String||||large,small|

## Events

|||||
|---|---|---|---|
|change||()|`@change`|


</template>
<script>
    export default {
        data() {
            return {
                num1: 1,
                num2: 1,
                num3: 1,
                num4: 1,
                num5: 1,
                num6: 1
            };
        },
        methods: {
            handleChange(val, oldVal) {
                console.log(`val: ${val}`);
                console.log(`oldVal: ${oldVal}`);
            }
        }
    };
</script>
<style>
    .demo-input-number .x-input-number+.x-input-number{
        margin-left : 15px;
    }
</style>


