<template lang="md">
# Dialog 

XCUI`$Dialog`, 

> XCUI`Vue.prototype``$Dialog`, `this.$Dialog.alert('message')`
> `import Dialog from 'xcui/components/dialog'` 
> DialogXCUI[Vue ](https://vuejs.org/v2/guide/plugins.html), ****`Vue.use(Dialog)` 



## alert

::: demo `this.$Dialog.alert(options)`API

```html

<tpl>
    <x-button type="primary" @click="dialog1"></x-button>
</tpl>

<script>
    export default {
        methods: {
            dialog1() {
                this.$Dialog.alert('', function () {
                    this.$Message.info('');
                });
            }
        }
    };
</script>

```

:::

## confirm

::: demo `this.$Dialog.confirm(options)`API

```html

<tpl>
    <x-button type="primary" @click="dialog2"></x-button>
</tpl>

<script>
    export default {
        methods: {
            dialog2() {
                this.$Dialog.confirm('', '', function () {
                    this.$Message.info('');
                }, function () {
                    this.$Message.warning('');
                });
            }
        }
    };
</script>
```

:::

## 

::: demo `this.$Dialog.show(options)`API

```html

<tpl>
    <x-button type="primary" @click="dialog3"></x-button>
</tpl>

<script>
    export default {
        methods: {
            dialog3() {
                this.$Dialog.show({
                    title: '',
                    content: '',
                    maskClosable: true,
                    showCloseButton: true,
                    showOkButton: true,
                    showCancelButton: true,
                    showHeader: true,
                    showFooter: true,
                    size: 'large',
                    onOk() {
                        this.$Message.info('');
                    },
                    onCancel() {
                        this.$Message.warning('');
                    }
                });
            }
        }
    };
</script>

```

:::


## API

###  **this.$Dialog.$alert(content, onOk)**

|  |  |  |  |  |
|-----|-----|-----|-----|----|
|content|String|-||****|
|onOk|Function|-|||

### **this.$confirm(title, content, onOk, onCancel, isRawHTML)**

|  |  |  |  |  |
|-----|-----|-----|-----|----|
|title|String|-|||
|content|String|-|isRawHTMLHTML||
|onOk|Function|-|||
|onCancel|Function|-|||
|options|Object|-|content(String)HTML||

###  **this.$Dialog.show(options)**

|  |  |  |  |  |
|-----|-----|-----|-----|----|
|content|String|-|slotcontent||
|title|String|-|slotheadertitle||
|maskClosable|Boolean|true|||
|scrollable|Boolean|false|Modal||
|showCloseButton|Boolean|true|||
|showOkButton|Boolean|true|||
|showCancelButton|Boolean|true|||
|showHeader|Boolean|true|||
|showFooter|Boolean|true|||
|okText|String||||
|cancelText|String||||
|size|String|middle|small/middle/large/full||
|className|String|-|||
|styles|Object|-|.modal-wrapper||
|contentStyle|object|-|.Modal-body||

### **this.$Dialog.close()**

`this.$Dialog.close()`







</template>

<script>
    export default {
        methods: {
            dialog1() {
                this.$Dialog.alert('', function () {
                    this.$Message.info('');
                });
            },
            dialog2() {
                this.$Dialog.confirm('', '', function () {
                    this.$Message.info('');
                }, function () {
                    this.$Message.warning('');
                });
            },
            dialog3() {
                this.$Dialog.show({
                    title: '',
                    content: '',
                    maskClosable: true,
                    showCloseButton: true,
                    showOkButton: true,
                    showCancelButton: true,
                    showHeader: true,
                    showFooter: true,
                    size: 'large',
                    onOk() {
                        this.$Message.info('');
                    },
                    onCancel() {
                        this.$Message.warning('');
                    }
                });
            }
        }
    };
</script>


