<template lang="md">
# Textarea 

## 



## 

::: demo `<x-textarea>``v-model``textarea``100%`, `2`

```html

<tpl>
    <x-row>
        <x-col :span="8">
            <x-textarea placeholder="" v-model="input"></x-textarea>
        </x-col>
    </x-row>
</tpl>

```

:::

## 

::: demo `disabled`

```html

<tpl>
    <x-row>
        <x-col :span="8">
            <x-textarea placeholder="" v-model="input1" :disabled="true"></x-textarea>
        </x-col>
    </x-row>
</tpl>

```

:::

## 

::: demo `rows``textarea`

```html

<tpl>
    <x-row>
        <x-col :span="8">
            <x-textarea placeholder="" v-model="input2" :rows="4"></x-textarea>
        </x-col>
    </x-row>
</tpl>

```

:::


## 

::: demo `autosize``textarea``Boolean(true, false)``minRows()`, `maxRows``Object`

```html

<tpl>
    <x-row :gutter="32">
        <x-col :span="8">
            <x-textarea placeholder="" v-model="input3" :autosize="true"></x-textarea>
            <p></p>
        </x-col>
        <x-col :span="8">
            <x-textarea placeholder="" v-model="input4" :autosize="{minRows: 2, maxRows: 4}"></x-textarea>
            <p>24</p>   
        </x-col>
    </x-row>
</tpl>

```

:::

## 

::: demo `maxlength``show-max``true`

```html

<tpl>
    <x-row>
        <x-col :span="8">
            <x-textarea placeholder="" v-model="input5" :maxlength="15" show-max></x-textarea>
        </x-col>
    </x-row>
</tpl>

```

:::



## Props
|  |  |  |  |  ||
|-----|-----|-----|-----|----|----|----|
|value|String, Number||`v-model`|||
|placeholder|String|||||
|autosize|Boolean,Object||Object`minRows``maxRows`|||
|disabled|Boolean|false||||
|readonly|Boolean|false||||
|autofocus|Boolean|false||||
|name|String|||||
|maxlength|Number|||||
|minlength|Number|||||

## Events

|||||
|---|---|---|---|
|change||event|`@change`|
|focus|||`@focus`|
|blur|||`@blur`|


</template>
<script>
    export default {
        data() {
            return {
                input: '',
                input1: '',
                input2: '',
                input3: '',
                input4: '',
                input5: ''
            };
        }
    };   
</script>



