<template lang="md">
# Button 




## 

::: demo  `default`, `primary`, `info`, `success`, `warning`, `danger``dashed``link``default`

```html

<tpl>
    <x-button>default</x-button>
    <x-button type="primary">primary</x-button>
    <x-button type="info">info</x-button>
    <x-button type="success">success</x-button>
    <x-button type="warning">warning</x-button>
    <x-button type="danger">danger</x-button>
    <x-button type="dashed">dashed</x-button>
    <x-button type="link">link</x-button>
</tpl>

```

:::


## 

::: demo `Button`  `Icon`  `icon`  `Button`  `Icon` 

```html

<tpl>
    <x-button icon="search"></x-button>
    <x-button icon="edit" type="primary"></x-button>
    <x-button icon="checkmark" type="primary"></x-button>
    <x-button icon="plus-round" type="primary"></x-button>
    <x-button icon="search" type="info"></x-button>
    <x-button icon="search" type="info"></x-button>
    <x-button icon="archive" type="success"></x-button>
</tpl>

```

:::

## 

::: demo `disabled`

```html

<tpl>
    <x-button :disabled="true"></x-button>
    <x-button type="primary" :disabled="true"></x-button>
    <x-button type="dashed" :disabled="true"></x-button>
    <x-button type="link" :disabled="true"></x-button>
</tpl>

```

:::

## 

::: demo `size`

```html

<tpl>
    <x-button size="small">small</x-button>
    <x-button></x-button>
    <x-button size="large">large</x-button>
</tpl>

```

:::

## 

::: demo `loading``button` ****
            

```html

<tpl>
    <x-button size="small" type="primary" :loading="true">Loading...</x-button>
    <x-button type="info" :loading="true">Loading...</x-button>
    <x-button type="primary" :loading="isLoadingA" @click="isLoadingA = true"></x-button>
    <x-button type="info" icon="heart" :loading="isLoadingB" @click="isLoadingB = true">{{isLoadingB ? '...' : ''}}</x-button>
</tpl>
<script>
    export default {
        data() {
            return {
                isLoadingA: false
                isLoadingB: false
            }
        }
    }
</script>

```

:::


##  

::: demo 

```html

<tpl>
    <x-button-group>
        <x-button type="primary" icon="chevron-left"></x-button>
        <x-button type="primary" icon="chevron-right"></x-button>
    </x-button-group>
    <x-button-group>
        <x-button icon="refresh" type="info"></x-button>
        <x-button icon="information" type="info"></x-button>
        <x-button icon="edit" type="info"></x-button>
    </x-button-group>
    <x-button-group>
        <x-button>Cancel</x-button>
        <x-button type="info">OK</x-button>
    </x-button-group>
</tpl>

```

:::

## Props

|  |  |  |  |  ||
|-----|-----|-----|-----|----|----|----|
|size|String||||small, large|
|type|String||||primary, info, success, warning, danger, dashed, link|
|loading|Boolean|false|||true , false|
|disabled|Boolean|false|||true , false|
|icon|String||icon|||

## Events
|||||
|---|---|---|---|
|click||`event`|`@click`|

</template>

<script>
    export default {
        data() {
            return {
                isLoadingA: false,
                isLoadingB: false
            };
        },
        methods: {
        }
    };

</script>




