## 

::: demo 

```html

<tpl>
    <x-tag v-for="tag in tags" :key="tag.name" closeable>{{tag.name}}</x-tag>
    <x-button type="sm" @click="addTag"></x-button>
</tpl>

<script>
export default {
    data() {
        return {
            index: 1
        };
    },
    methods: {
        addTag() {
            if (this.tags === null) {
                this.tags = [];
            }
            this.tags.push({name: `${this.index++}`});
        }
    }
};

</script>

```

:::

## checkbox)

::: demo , `x-tag-checkable``@change`tagVue`checked`

```html

<tpl>
    <x-tag-checkable v-for="tag in tags2"
        :color="tag.color"
        :key="tag.key"
        :tag-key="tag.key"
        :name="tag.name"
        :checked="tag.checked"
        @change="change1">{{tag.name}}</x-tag-checkable>
    <p style="margin-top: 15px;">{{selectedTagNames.length > 0 ? selectedTagNames : ''}}</p>
    <p style="margin-top: 15px;">{{selectedTagKeys.length > 0 ? selectedTagKeys : ''}}</p>
</tpl>

<script>
export default {
    data() {
        return {
            tags:[{
                name: '', color: ''
            }]
            selectedTags: [],
            selectedTagNames: [],
            selectedTagKeys: [],
            tags2: [{
                name: '', color: '', key: '0', checked: true
            }, {
                name: '', color: 'pink', key: '1'
            }, {
                name: '', color: 'green', key: '2', checked: true
            }, {
                name: '', color: 'blue', key: '3'
            }, {
                name: '', color: 'purple', key: '4'
            }]
        };
    },
    methods: {
        change1(tag, checked) {
            if (checked) {
                let hit;
                this.selectedTags.forEach((v) => {
                    if (v.tagkey === tag.tagkey) {
                        hit = true;
                    }
                });
                if (!hit) {
                    this.selectedTags.push({
                        name: tag.name,
                        color: tag.color,
                        tagkey: tag.tagKey
                    });
                }
            }
            else {
                let i = -1;
                this.selectedTags.forEach((v, index) => {
                    if (v.tagkey === tag.tagKey) {
                        i = index;
                    }
                });
                if (i >= 0) {
                    this.selectedTags.splice(i, 1);
                }
            }
            this.selectedTagNames = this.selectedTags.map((v) => {
                return v.name;
            });
            this.selectedTagKeys = this.selectedTags.map((v) => {
                return v.tagkey;
            });
        }
    }
};

</script>

```

:::



## Tag Props
|  |  |  |  |  | |
|-----|-----|-----|-----|----|----|
|color|String||||pink/red/orange/green/cyan/blue/purple, `-inverse`|
|tag-key|String, Number||tag|||
|closeable|Boolean|false|||true, false|
|on-close|function||eventtag|||
|transition|String|fade||||

## TagCheckable Props
|  |  |  |  |  | |
|-----|-----|-----|-----|----|----|
|color|String||||pink/red/orange/green/cyan/blue/purple, `-inverse`|
|name|String|false|||true, false|
|tag-key|String, Number||tag|||
|checked|Boolean|false|||true,false|


## TagCheckable Events
|||||
|---|---|---|---|
|change||(TagVueComponent, checked)|`@change`|



</template>

<script>
export default {
    data() {
        return {
            tags: [{name: '', key: 'test'}],
            showTag: false,
            index: 1,
            key: 'data',
            selectedTags: [],
            selectedTagNames: [],
            selectedTagKeys: [],
            tags2: [{
                name: '', color: '', key: '0', checked: true
            }, {
                name: '', color: 'pink', key: '1'
            }, {
                name: '', color: 'green', key: '2'
            }, {
                name: '', color: 'blue', key: '3'
            }, {
                name: '', color: 'purple', key: '4'
            }]
        };
    },
    methods: {
        close1(e, tagInst) {
            this.$Message.info(`tagKey${tagInst.tagKey}`);
        },
        close2(e) {
            e.preventDefault();
            this.$Message.info('');
        },
        closeclick(e) {
            e.preventDefault();
        },
        addTag() {
            if (this.tags === null) {
                this.tags = [];
            }
            this.tags.push({name: `${this.index++}`, key: this.index});
        },
        onCloseTag(e, tagInst) {
            console.log(tagInst.tagKey);
        },
        change1(tag, checked) {
            if (checked) {
                let hit;
                this.selectedTags.forEach((v) => {
                    if (v.tagkey === tag.tagkey) {
                        hit = true;
                    }
                });
                if (!hit) {
                    this.selectedTags.push({
                        name: tag.name,
                        color: tag.color,
                        tagkey: tag.tagKey
                    });
                }
            }
            else {
                let i = -1;
                this.selectedTags.forEach((v, index) => {
                    if (v.tagkey === tag.tagKey) {
                        i = index;
                    }
                });
                if (i >= 0) {
                    this.selectedTags.splice(i, 1);
                }
            }
            this.selectedTagNames = this.selectedTags.map((v) => {
                return v.name;
            });
            this.selectedTagKeys = this.selectedTags.map((v) => {
                return v.tagkey;
            });
        }
    }
};

</script>




