```html
<tpl>
    <x-row gutter="32">
        <x-col span="4">
            <x-input placeholder="Large Size" v-model="input1" size="large"></x-input>
        </x-col>
        <x-col span="4">
            <x-input placeholder="Default Size" v-model="input2"></x-input>
        </x-col>
