## 

::: demo `input``prepend``append`slotslot

```html
<tpl>
    <x-row>
        <x-col span="8">
            <x-input placeholder="">
                <span slot="prepend">http://</span>
                <span slot="append">.com</span>
            </x-input>
        </x-col>
    </x-row>
    <x-row style="margin-top: 15px">
        <x-col span="8">
            <x-input placeholder="">
                <span slot="append"><x-icon name="share"></x-icon></span>
            </x-input>
        </x-col>
    </x-row>
    <x-row style="margin-top: 15px">
        <x-col span="8">
            <x-input placeholder="">
                <x-select v-model="model" slot="prepend" placeholder="" style="width: 100px;">
                    <x-option v-for="item in options" :key="item.key" :value="item.value"></x-option>
                </x-select>
                <span slot="append"><x-icon name="share"></x-icon></span>
            </x-input>
        </x-col>
    </x-row>
</tpl>
```

:::

## Props
|  |  |  |  |  ||
|-----|-----|-----|-----|----|----|----|
|v-model|String, Number||`v-model`|||
|size|String||||`large`, `small`|
|placeholder|String|||||
|type|String||||`text`, `password`|
|disabled|Boolean|false||||
|icon|String|||||
|readonly|Boolean|false||||
|autofocus|Boolean|false||||
|name|String|||||
|form|String|||||
|maxlength|Number|||||
|minlength|Number|||||
|iconClick|Function||`icon`|||

## Events

|||||
|---|---|---|---|
|click|`icon`||`iconclick`|
|enter|||`@enter`|
|input|||`@input`|
|change||event|`@change`|
|focus|||`@focus`|
|blur|||`@blur`|

## Slot

|||
|---|---|
|prepend||
|append||

</template>

