<template lang="md">
# Checkbox 

## 



## 

::: demo `x-checkbox``v-model``Boolean`, `true`

```html

<tpl>
    <x-checkbox v-model="checked0"></x-checkbox>
</tpl>

<script>
    export default {
        data() {
            return {
                checked0: false,
            };
        }
    };
</script>

```

:::

## 

::: demo `disabled`

```html

<tpl>
    <x-checkbox v-model="checked2" :disabled="true"></x-checkbox>
    <x-checkbox v-model="checked3" :disabled="true"></x-checkbox>
</tpl>

<script>
    export default {
        data() {
            return {
                checked2: true,
                checked3: false
            };
        }
    };
</script>

```

:::

## 

::: demo `x-checkbox``x-checkbox-group`

```html

<tpl>
    <x-checkbox-group v-model="checklist">
        <x-checkbox value=""></x-checkbox>
        <x-checkbox value=""></x-checkbox>
        <x-checkbox value=""></x-checkbox>
        <x-checkbox value="" :disabled="true"></x-checkbox>
        <x-checkbox value="" :disabled="true"></x-checkbox>
    </x-checkbox-group>
    <p style="margin-top: 15px;">
         {{checklist}}
    </p>
</tpl>

<script>
    export default {
        data() {
            return {
                checklist: ['', '']
            };
        }
    };
</script>

```

:::

## 

::: demo `indeterminate`

```html

<tpl>
    <x-checkbox :indeterminate="indeterminate" v-model="checkAll" @change="handleCheckAll"></x-checkbox>
    <x-checkbox-group v-model="checkedRoles" @change="handleCheckRole" style="display:inline-block; margin-left: 15px;">
        <x-checkbox value=""></x-checkbox>
        <x-checkbox value=""></x-checkbox>
        <x-checkbox value=""></x-checkbox>
        <x-checkbox value=""></x-checkbox>
    </x-checkbox-group>
</tpl>

<script>
    export default {
        data() {
            return {
                checkedRoles: [''],
                indeterminate: true,
                checkAll: false,
                roleList: ['', '', '', '']
            };
        },
        methods: {
            handleCheckAll(e) {
                this.checkedRoles = e.target.checked ? this.roleList : [];
            },
            handleCheckRole(val) {
                let count = val.length;
                this.checkAll = count === this.roleList.length;
                this.indeterminate = count > 0 && count < this.roleList.length;
            }
        }
    };
</script>

```

:::


## Checkbox Props
|  |  |  |  |  ||
|-----|-----|-----|-----|----|----|----|
|v-model|Boolean||||true,false|
|value|String, Number, Boolean|||`value`||
|disabled|Boolean|false|||`true`, `false`|
|interminate|Boolean|false||||

## Checkbox Events

|||||
|---|---|---|---|
|change||`event`|`@change`|

## Checkbox Slot

|||
|---|---|
|default|`<x-checkbox></x-checkbox>`|

## CheckboxGroup Props
|  |  |  |  |  ||
|-----|-----|-----|-----|----|----|----|
|v-model|Array|||||

## CheckboxGroup Events
|||||
|---|---|---|---|
|change||`event`|`@change`|

    

</template>
<script>
    export default {
        data() {
            return {
                checked0: false,
                checked1: true,
                checked2: true,
                checked3: false,
                checklist: ['', ''],
                checkedRoles: [''],
                indeterminate: true,
                checkAll: false,
                roleList: ['', '', '', '']
            };
        },
        methods: {
            handleCheckAll(e) {
                this.checkedRoles = e.target.checked ? this.roleList : [];
            },
            handleCheckRole(val) {
                let count = val.length;
                this.checkAll = count === this.roleList.length;
                this.indeterminate = count > 0 && count < this.roleList.length;
            }
        }
    };
</script>


